import React from 'react'

function Section1({handleChange}) {
  return (
    <div className="section1">
      <div className="header">
        <h2>Spotings Appointment Sheduler</h2>
      </div>
      <div className="tracker">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className="sec1">
        <p>Welcome to the Spotings appointment sheduler</p>
        <p>you can simply add an event to your google calendar</p>
        <p>quick start</p>
      </div>
      <div className="pageChange">
        <button onClick={()=> handleChange("next")}>Next</button>
      </div>
    </div>
  )
}

export default Section1
