import React, { useState } from 'react';
import './App.css';
import Section1 from "./Components/Section1";
import Section2 from "./Components/Section2";
import Section3 from "./Components/Section3";
import Section4 from "./Components/Section4";
require("dotenv").config();

function App() {
  const [ page, setPage ] = useState(0);
  const [ fromDate, setFromDate ] = useState(null);
  const [ fromTime, setFromTime ] = useState(null);
  const [ toDate, setToDate ] = useState(null);
  const [ toTime, setToTime ] = useState(null);
  const [ description, setDescription ] = useState("");


 var gapi = window.gapi;
 var DISCOVERY_DOCS = ["https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest"];
 var SCOPES = "https://www.googleapis.com/auth/calendar.events";
  
    const handleClick = () => {
      gapi.load('client:auth2', () => {
        console.log('loaded client')
        gapi.client.init({
          apiKey: process.env.API_KEY,
          clientId: "106108581713-ft2e09rsp3or5546p9hsn95gmj3ub6bt.apps.googleusercontent.com",
          discoveryDocs: DISCOVERY_DOCS,
          scope: SCOPES,
        })
  
        gapi.client.load('calendar', 'v3', () => console.log('bam!'))
  
        gapi.auth2.getAuthInstance().signIn()
        .then(() => {
          var event = {
            'summary': 'appointment scheduler',
            'description': description,
            'start': {
              'dateTime': `${fromDate}T${fromTime}:00-00:00`
            },
            'end': {
              'dateTime': `${toDate}T${toTime}:00-00:00`
            }
          }

          var request = gapi.client.calendar.events.insert({
            'calendarId': 'primary',
            'resource': event,
          })
  
          request.execute(event => {
            console.log(event)
            window.open(event.htmlLink)
          })

          setPage(page + 1);
        })
    })
  }
  const handleDate = (e)=>{
    if(e.target.name === "fromDate"){
      setFromDate(e.target.value);
    }else if(e.target.name === "fromTime"){
      setFromTime(e.target.value);
    }else if(e.target.name === "toDate"){
      setToDate(e.target.value);
    }else if(e.target.name === "toTime"){
      setToTime(e.target.value);
    }else if(e.target.name === "description"){
      setDescription(e.target.value);
    }
  }

 
  const handleChange = (e)=>{
    if(e === "prev"){
      setPage(page - 1);
    }
    if(e === "next"){
      setPage(page + 1);
    }
    if(e === "refresh"){
      setPage(0);
    }
  }

    if(page === 0){
    return < Section1 handleChange={handleChange}/>
    }else if(page === 1){
      return < Section2 handleChange={handleChange} handleDate={handleDate} descrip={description}/>
    }else if(page === 2){
    return  < Section3 handleClick={handleClick} handleChange={handleChange} fromDate={fromDate} fromTime={fromTime} toDate={toDate} toTime={toTime}  descrip={description}/>
    }else if(page === 3){
      return  < Section4 handleChange={handleChange}/>
    }
}

export default App;
