import React from 'react'

function Section2({handleChange, handleDate, descrip}) {
  return (
    <div className="section2">
      <div className="header">
        <h2>Spotings Appointment Sheduler</h2>
      </div>
      <div className="tracker">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className="time">
        <h2>select date for appointment</h2>
        <div className="start">
          <div>
            <span>from</span>
            <input type="date" name="fromDate" onChange={handleDate}/>
          </div>
          <div>
            <span>time</span>
            <input type="time" name="fromTime" onChange={handleDate}/>
          </div>
        </div>
        <div className="to">
          <div>
            <span>to</span>
            <input type="date" name="toDate" onChange={handleDate}/>
          </div>
          <div>
            <span>time</span>
            <input type="time" name="toTime" onChange={handleDate}/>
          </div>
        </div>
      </div>
      <div className="descreption">
        <input type="text" placeholder="description" onChange={handleDate} name="description" value={descrip}/>
      </div>
      <div className="pageChange">
        <button onClick={()=> handleChange("prev")}>previous</button>
        <button onClick={()=> handleChange("next")}>next</button>
      </div>
    </div>
  )
}

export default Section2
