import React from 'react'

function Section3({handleChange, handleClick,fromDate,fromTime,toDate,toTime,descrip}) {
  return (
    <div className="section3">
      <div className="header">
        <h2>Spotings Appointment Sheduler</h2>
      </div>
      <div className="tracker">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className="confirm">
        <h2>confirm your appointment</h2>
        <p><span>From:</span> {fromDate} : {fromTime}</p>
        <p><span>To:</span> {toDate} : {toTime}</p>
        <p><span>Description:</span>{descrip}</p>
      </div>
      <div className="pageChange">
        <button onClick={()=> handleChange("prev")}>Cancel</button>
        <button onClick={()=> handleClick()}>Confirm</button>
      </div>
    </div>
  )
}

export default Section3
